import React from "react";
import "./app.css";
import Color from "./Components/Color";

class App extends React.Component {
  constructor() {
    super();
    this.state = {
      backcolor: "gray"
    };
  }
  change = () => {
    this.setState(
      {
        backcolor: this.props.color
      },
      () => {
        console.log("hello");
      }
    );
  };
  change = (colorvalue) => {
    this.setState({
      backcolor: colorvalue
    });
  };
  render() {
    return (
      <>
        <div className="text">
          <h1 className="textcolor">Color Picker</h1>
        </div>
        <div className="colorBoxContainer">
          <Color color="red" onClick={this.change} />
          <Color color="yellow" onClick={this.change} />
          <Color color="green" onClick={this.change} />
          <Color color="blue" onClick={this.change} />
          <Color color="pink" onClick={this.change} />
        </div>

        <div className="bigColorContainer">
          <div
            className="colorContainer"
            style={{ backgroundColor: this.state.backcolor }}
          ></div>
        </div>
      </>
    );
  }
}
export default App;
