import React from "react";
import "./Color.css";

class Color extends React.Component {
  constructor(props) {
    super(props);
  }
  render() {
    console.log(this.props.color);
    return (
      <>
        <div
          className="colorbox"
          onClick={() => {
            this.props.onClick(this.props.color);
          }}
          style={{ backgroundColor: this.props.color }}
        ></div>
      </>
    );
  }
}
export default Color;
